.header {
  height: 34vw;
  margin: 30px auto;
  background: url("../../../public//header_image.png") no-repeat;
  background-size: contain;
  position: relative;
}
.header-contents {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 1.5vw;
  max-width: 50%;
  bottom: 10%;
  left: 6vw;
  animation: fadeIn 3s;
}
.header-contents h2 {
  font-weight: 500;
  color: white;
  font-size: max(4.5vw, 22px);
}
.header-contents p {
  color: white;
  font-size: 1vw;
}
.header-contents button {
  border: none;
  color: #3e3e3f;
  font-weight: 500;
  padding: 1vw 2.3vw;
  background-color: white;
  font-size: max(1vw, 13px);
  border-radius: 50px;
}
.header-contents button:hover {
  background-color: #ff965e;
  color: #000;
}
@media (max-width: 1050px) {
  /* .header-contents {
    max-width: 45%;
  } */
  .header-contents p {
    font-size: 13px;
  }
}
@media (max-width: 787px) {
  .header {
    position: static;
  }
  .header-contents {
    position: static;
    align-items: flex-start;
    padding-left: 30px;
  }
  .header-contents button {
    padding: 2vw 3vw;
  }
  .header-contents h2 {
    padding-top: 30px;
  }
  .header-contents p {
    display: none;
  }
}
@media (max-width: 750px) {
  .header-contents {
    max-width: 65%;
    /* padding-top: 20px; */
  }
  .header-contents p {
    display: none;
  }
  .header-contents button {
    padding-top: 2vw 4vw;
  }
}
@media (max-width: 420px) {
  .header-contents h2 {
    font-size: 18px;
  }
}
@media (max-width: 370px) {
  .header-contents h2 {
    font-size: 15px;
  }
}
@media (max-width: 380px) {
  .header-contents h2 {
    font-size: 15px;
  }
}
@media (max-width: 310px) {
  .header-contents h2 {
    font-size: 12px;
  }
}
@media (max-width: 290px) {
  .header-contents h2 {
    font-size: 10px;
  }
}
